/* 27-28rem thumbnail */

#video-wrapper{
    padding: 0px 4rem;
    padding-right: 60px;
    overflow-y: auto;
    padding-top: 24px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-row-gap: 3rem;
    height: 100%;
}

#video-wrapper::-webkit-scrollbar {
    width: 8px;
    transition: 0.3s;
  }

  #video-wrapper::-webkit-scrollbar-track {
    background-color: none;
  }
  
  #video-wrapper::-webkit-scrollbar-thumb {
    background-color: gray;
    border: none;
    border-radius: 10px;
  }
  

/* video card */
.card{
    width: 22rem;
    max-width: 35rem;
    color: white;
    cursor: pointer;
}

.card .upper{
    width: 100%;
    height: 12rem;
}

.card .upper a img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
}

.card .lower{
    display: flex;
    margin-top: 10px;
}

/* .card .lower .video-left{

} */

.card .lower .video-left a img{
    width: 32px;
    height: 32px;
    border-radius: 50%;
}

.card .lower .video-right{
    margin-left: 12px;
    padding-right: 10px;
    width: 100%;
}

.card .lower .video-right a{
    text-decoration: none;
    color: white;
}

.card .lower .video-right a h3{
    font-size: 18px;
    margin-bottom: 5px;
    color: white;
    font-weight: 600;
}

.card .lower .video-right a span{
    color: rgba(255, 255, 255, 0.623);
    transition: 0.3s;
}

.card .lower .video-right a span:hover{
    color: rgb(255, 255, 255);
}

.card .lower .video-right p{
    margin-top: 3px;
    color: rgba(255, 255, 255, 0.623);
}

.card .lower .video-right p .views{
    margin-right: 10px;
}