#container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1000;
}


/* start */
#container .start {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 28px;
}

#container .start button {
    background-color: transparent;
    font-size: 20px;
    color: white;
    border: none;
    margin-right: 20px;
    font-weight: 100 !important;
    cursor: pointer;
}

#container a {
    text-decoration: none;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    letter-spacing: -1px;
    font-weight: bold;
    cursor: pointer;
}

#container a span {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 3px;
}

#container a span img {
    width: 32px;
    height: auto;
}

/* center */
#container .center {
    display: flex;
    justify-content: center;
    align-items: center;
}

#container .center .group {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 38rem;
    height: 38px;
}

#container .center .group input {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: #121212;
    border: 1px solid #222222;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    padding: 10px 20px;
    color: white;
    z-index: 50;
    font-size: 18px;
}

#container .center .group input::-webkit-search-decoration{
    background-color: red;
}

#container .center .group input::placeholder {
    font-size: 15px;
    color: #7c7f83;
}


#container .center .group button {
    margin: 0;
    padding: 5px 23px;
    background-color: #222222;
    color: #b8b8b8;
    border: none;
    font-size: 20px;
    height: 100%;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-top: #222222;
    border-right: #222222;
    border-bottom: #222222;
    border-left: none;
    cursor: pointer;
}

#container .center .micro {
    margin-left: 20px;
    font-size: 20px;
    background-color: #222222;
    border-radius: 50%;
    padding: 12px 15px;
    cursor: pointer;
}


/* end */
#container .end {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 28px;
}

#container .end .space {
    margin: 0px 15px;
    font-size: 20px;
    cursor: pointer;
}

#container .end img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 0px !important;
}