.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.wrapper .float-right {
    float: right;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.wrapper .float-right .player {
    width: 55rem;
    height: 30rem;
    margin-right: 25px;
}

.player .img {
    width: 100% !important;
    height: 100% !important;
}

.wrapper .details {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    width: 100%;
}

.wrapper .details h3 {
    padding-left: 6rem;
    font-size: 20px;
    margin-bottom: 1rem;
    width: 87%;
}

.wrapper .details .inner-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 2rem 0px 6rem;
}

.wrapper .details .inner-details button.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #222222;
    padding: 8px 13px;
    border: none;
    color: white;
    font-size: 15px;
    margin: 0px 5px;
    border-radius: 20px;
    cursor: pointer;
    transition: 0.3s;
}

.wrapper .details .inner-details button:hover{
    background-color: rgba(128, 128, 128, 0.667);
}

.wrapper .details .inner-details button.btn span {
    margin-left: 5px;
}

.wrapper .details .inner-details .start {
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrapper .details .inner-details .start img {
    width: 46px;
    height: 46px;
    border-radius: 50%;
    margin-right: 14px;
}

.wrapper .details .inner-details .start .inner-channel .name {
    margin-bottom: 2px;
}

.wrapper .details .inner-details .start .inner-channel a {
    text-decoration: none;
    color: white;
    font-size: 20px;
    margin-bottom: 16px;
}

.wrapper .details .inner-details .start .inner-channel p.subscriber{
    color: gray;
}

.wrapper .details .inner-details .center {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.wrapper .details .inner-details .end {
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrapper .details .inner-details .end .btn-group {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #222222;
    padding: 8px 13px;
    border: none;
    color: white;
    font-size: 15px;
    border-radius: 20px;
    margin: 0px 5px;
}

.wrapper .details .inner-details .end .btn-group:hover {
    background-color: rgba(128, 128, 128, 0.667);
}

.wrapper .details .inner-details .end .btn-group button {
    margin: 0;
    background-color: #222222;
    padding: 0px;
    border: none;
    transition: 0.3s;
    background-color: transparent;
}

.wrapper .details .inner-details .end .btn-group button.btn-space {
    padding-right: 10px;
    border-radius: 0px;
    border-right: 2px solid gray;
}

.wrapper .details .inner-details .end .btn-group button span{
    margin-left: 10px;
}

.wrapper .details .inner-details .end .btn-group button.btn-space-right {
    padding-left: 10px;
    border-radius: 0px;
}

.wrapper .details .inner-details .end .btn-circle{
    border-radius: 50%;
    padding: 9px 9px;
}

.description{
    background-color: #222222;
    color: white;
    margin: 1rem 6rem;
    margin-right: 2rem;
    padding: 10px 10px;
    border-radius: 10px;
    font-size: 17px;
    width: 53rem;
}

.description p span.views{
  margin-right: 8px;
}