#specific #overflowvertical{
    position: fixed;
    top: 5rem;
    z-index: 100;
    width: 100%;
    display: flex;
    justify-content: center;
    height: 91vh;
    overflow-y: auto;
}

#overflowvertical::-webkit-scrollbar {
    width: 8px;
    transition: 0.3s;
}

#overflowvertical::-webkit-scrollbar-track {
    background-color: none;
}

#overflowvertical::-webkit-scrollbar-thumb {
    background-color: gray;
    border: none;
    border-radius: 10px;
}

#specific .combination .left{
    width: 67%;
}

#specific .combination .right{
    width: 33%;
    padding-right: 2rem;
}