#wrapper {
    display: flex;
    justify-content: center;
    position: fixed;
    top: 60px;
    z-index: 100;
    width: 100%;
}

#wrapper .left {
    width: 17%;
    height: 91vh;
}

#wrapper .right {
    width: 83%;
    height: 91vh;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding-right: 1rem;
}

#wrapper .right::-webkit-scrollbar {
    width: 8px;
    transition: 0.3s;
}

#wrapper .right::-webkit-scrollbar-track {
    background-color: none;
}

#wrapper .right::-webkit-scrollbar-thumb {
    background-color: gray;
    border: none;
    border-radius: 10px;
}

#wrapper .combinator {
    overflow-y: auto;
    padding: 0px 4rem;
    padding-right: 60px;
    padding-top: 24px;
}

/* channel banner */
.chan-upper .banner {
    width: 100%;
    height: 12rem;
}

.chan-upper .banner img {
    width: 100%;
    height: 100%;
}

.chan-upper .channel-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 8rem;
}

.chan-upper .channel-details .start {
    display: flex;
    justify-content: center;
    align-items: center;
}

.chan-upper .channel-details .start .img {
    margin-right: 25px;
}

.chan-upper .channel-details .start .img img {
    width: 9rem;
    height: 9rem;
    border-radius: 50%;
}

.chan-upper .channel-details .start .chandetails .space {
    margin-bottom: 7px;
}

.chan-upper .channel-details .start .chandetails span {
    margin-right: 8px;
}

.chan-upper .channel-details .end button {
    padding: 10px 25px;
    color: #222222;
    background-color: white;
    border: none;
    font-size: 16px;
    border-radius: 20px;
    cursor: pointer;
}

.chan-lower {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 3rem;
    padding: 0px 4rem;
    padding-right: 60px;
    padding-top: 24px;
}

/* video card */
#channel-card {
    width: 22rem;
    color: white;
    cursor: pointer;
}

#channel-card .upper {
    width: 100%;
    height: 12rem;
}

#channel-card .upper a img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
}

#channel-card .lower {
    margin-top: 10px;
}

#channel-card .lower .video-right {
    padding-right: 10px;
    width: 100%;
}

#channel-card .lower .video-right a {
    text-decoration: none;
    color: white;
}

#channel-card .lower .video-right a h3 {
    font-size: 18px;
    margin-bottom: 5px;
    color: white;
    font-weight: 600;
}

#channel-card .lower .video-right a span {
    color: rgba(255, 255, 255, 0.623);
    transition: 0.3s;
}

#channel-card .lower .video-right a span:hover {
    color: rgb(255, 255, 255);
}

#channel-card .lower .video-right p {
    margin-top: 3px;
    color: rgba(255, 255, 255, 0.623);
}

#channel-card .lower .video-right p .views {
    margin-right: 10px;
}