#sidebar-wrapper {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  height: 100%;
  overflow-y: auto;
}

#sidebar-wrapper::-webkit-scrollbar {
  width: 0;
  transition: 0.3s;
}

#sidebar-wrapper:hover::-webkit-scrollbar {
  width: 8px;
}

#sidebar-wrapper::-webkit-scrollbar-track {
  background-color: none;
}

#sidebar-wrapper::-webkit-scrollbar-thumb {
  background-color: gray;
  border: none;
  border-radius: 10px;
}


#sidebar-wrapper .special {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #333333;
  padding: 15px 0px;
}

#sidebar-wrapper .section1 {
  padding-top: 0px;
}

#sidebar-wrapper .special a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  line-height: 2rem;
  transition: 0.3s;
  cursor: pointer;
  padding: 5px 0px;
  padding-left: 18px;
  display: flex;
  align-items: center;
}

#sidebar-wrapper .special a.active {
  background-color: #222222;
  border-radius: 10px;
}

#sidebar-wrapper .special a:hover {
  background-color: #222222;
  border-radius: 10px;
}

#sidebar-wrapper .special a .span {
  font-size: 1.5rem !important;
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}


#sidebar-wrapper .section3 a .span img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

#sidebar-wrapper .section6{
  padding: 15px 0px;
  padding-left: 18px;

}


#sidebar-wrapper .section6 a {
  color: #ffffffa6;
  text-decoration: none;
  line-height: 23px;
  display: inline-block !important;
  font-size: 16px;
  margin-right: 20px;
  white-space: nowrap;
}

#sidebar-wrapper .section6 .wrap{
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
}

#sidebar-wrapper .section6 p{
  color: rgba(255, 255, 255, 0.37);
  font-size: 15px;
}