#category-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 40px;
    overflow-x: auto;
    padding: 5px 0px;
    margin-right: 28px;
    position: relative;
    scroll-behavior: smooth;
  }
  
  #category-wrapper::-webkit-scrollbar {
    display: none;
  }
  
  #category-wrapper button {
    font-size: 15px;
    border: none;
    border-radius: 8px;
    padding: 8px 12px;
    background-color: #272727;
    color: white;
    margin-left: 10px;
    white-space: nowrap;
    cursor: pointer;
  }

  #category-wrapper button:hover {
    background-color: #3c3939;
  }
  
  .left-arrow {
    position: sticky; /* Change to sticky positioning */
    left: 0;
    background-color: var(--body-background);
    font-size: 25px;
    padding: 13px 15px;
    box-shadow: 4px 0px 17px 15px var(--body-background);
    cursor: pointer;
    margin-right: 10px;
  }

  .right-arrow {
    position: sticky; /* Change to sticky positioning */
    right: 0;
    background-color: var(--body-background);
    font-size: 25px;
    padding: 13px 15px;
    box-shadow: -4px 0px 17px 15px var(--body-background);
    cursor: pointer;
  }
  