.cardd {
    display: flex;
    width: 100%;
    margin-bottom: 10px;
}

.cardd .video-left {
    width: 20rem;
    height: 105px;
}

.cardd .video-left img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.cardd .video-right{
   width: 100%;
   padding-left: 7px;
}

.cardd .video-right a {
    color: white;
    text-decoration: none;
}

.cardd .video-right a h3{
    font-size: 18px;
    margin-bottom: 5px;
    color: white;
    font-weight: 600;
}

.cardd .video-right a span{
    color: rgb(255 255 255 / 86%)
}

.cardd .video-right p{
    margin-top: 3px;
    color: rgb(255 255 255 / 86%)
}

.cardd .video-right p .views{
    margin-right: 10px;
}